<template>
  <div class="home">
    <v-app>
      <!-- <v-app-bar app light color="white" height="80">
      <div class="d-flex align-center">
        <v-img
          alt="Vuetify Logo"
          class="shrink mr-2"
          contain
          src="https://vutura.io/landing-page-vutura-v3/img/logo-vutura.svg"
          transition="scale-transition"
          width="140"
        />
        <div style="padding-right:0.5cm"></div>
      </div>


    </v-app-bar> -->

    <div style="padding-top:10%"></div>

    <!--------------LOGIN----------------------------------------->
    <template>
      <v-container
          fluid
        >
          <v-row
            align="center"
            justify="center"
          >
            <v-col
              cols="12"
              sm="12"
              md="4"
            >
              <v-card class="elevation-12" color="#F1EFFF" width="800" height="500">
                <v-toolbar
                  color="#F1EFFF"
                  flat
                >
                  
                  <v-spacer></v-spacer>
                  <div id="logo">
                    <img src="../assets/logo-vutura.png" id="logo-vutura">
                  </div>
                  <v-spacer></v-spacer>
                  

                </v-toolbar>

                <div style="padding-top:0.5cm"></div>
                  

                <v-card-text>
                  
                  <v-form
                    ref="form"
                    v-model="valid"
                    lazy-validation
                  >
                    <div style="padding-top:1cm"></div>
                    <v-toolbar flat color="#F1EFFF">
                      <v-spacer></v-spacer>
                      <v-text-field
                        v-model="username"
                        label="Username / Email"
                        :rules="usernameRules"
                        name="username"
                        solo
                        flat
                        class="my-input"
                      ></v-text-field>
                      <v-spacer></v-spacer> 
                    </v-toolbar>

                    <div style="padding-top:0.5cm"></div>

                    <v-toolbar flat color="#F1EFFF"> 
                      <v-spacer></v-spacer> 
                    <v-text-field
                      v-model="password"
                      id="password"
                      label="Password"
                      :rules="passwordRules"
                      v-on:keyup.enter="login"
                      name="password"
                      solo
                      flat
                      class="my-input"
                      type="password"
                    ></v-text-field>
                    <v-spacer></v-spacer> 
                    </v-toolbar>

                  </v-form>
                
                <!-- Ini untuk nampilin pesan error -->
                </v-card-text>
                  <v-alert type="error" 
                    :value="alert"
                  >
                    {{pesanError}}
                  </v-alert>
                <v-card-actions>
                  
                  <v-spacer></v-spacer>
                  <v-btn 
                    dark 
                    color="#FF6C00"
                    @click="login"
                    flat
                    width="200px"
                    height="50px"
                  >
                  Login Admin
                  </v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>

                <v-card-actions>
                  
                  <v-spacer></v-spacer>
                  <v-btn 
                    dark 
                    color="#FF6C00"
                    @click="loginAgent"
                    flat
                    width="200px"
                    height="50px"
                  >
                  Login Agent
                  </v-btn>
                  <v-spacer></v-spacer>
                
                </v-card-actions>

                  <v-layout row v-if="error">
                    <v-flex xs12 sm6 offset-sm3>
                      <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                  </v-layout>

                  <div style="padding-bottom:1cm"></div>
              </v-card>
            </v-col>
          </v-row>
        </v-container>

    </template>
    <!--------------End LOGIN----------------------------------------->
    </v-app>

  </div>
</template>


<script src="/socket.io/socket.io.js"></script>

<script>
import axios from 'axios'
export default {
  data: () => ({
    valid: true,
    username: '',
    password: '',
    pesanError: '',
    alert: false,
    usernameRules: [
      v => !!v || 'Masukan Username',
    ],
    passwordRules: [
      v => !!v || 'Masukan Password',
    ],
  }),

  methods: {
    login: function(){
      axios.post('http://localhost:3000/admin/login',{
          username: this.username,
          password: this.password
        })
        .then((Response) =>{
          const log = Response.data;
          sessionStorage.setItem('login',log.id);
          sessionStorage.setItem('user','admin');
          /* Register socket */
          /* socket.emit('register-as-admin', log.id); */
          if(log.msg == "Login Sukses"){
            axios.get('http://localhost:3000/admin/home')
          .then((r) => {
            const user = r.data.data;
            sessionStorage.setItem('userChat', JSON.stringify(user))
            console.log(JSON.parse(sessionStorage.getItem('userChat')))
          });
            window.location.href = "../Chat"
          }
          else if (log.msg != "Login Sukses"){
            this.pesanError= "Username atau Password Salah";
            this.alert= true;
          }
        })
        .catch((err) =>{
          /* untuk error 4xx */
          if (err.response){
            this.pesanError= "Username atau Password Salah";
            this.alert= true;
          }
          /* misal req error dll */
          else{
            this.pesanError= "Sistem Sedang Gangguan";
            this.alert= true;
          }
        })
    },

    loginAgent: function(){
      axios.post('http://localhost:3000/admin/agent/login',{
          email: this.username,
          password: this.password
        })
        .then((Response) =>{
          const log = Response.data;
          sessionStorage.setItem('login',log.id);
          sessionStorage.setItem('user','agent');
          /* Register socket */
          /* socket.emit('register-as-admin', log.id); */
          if(log.msg == "Login Sukses"){
            axios.get('http://localhost:3000/admin/home')
          .then((r) => {
            const user = r.data.data;
            sessionStorage.setItem('userChat', JSON.stringify(user))
            console.log(JSON.parse(sessionStorage.getItem('userChat')))
          });
            window.location.href = "http://localhost:8080/Chat"
          }
          else if (log.msg != "Login Sukses"){
            this.pesanError= "Username atau Password Salah";
            this.alert= true;
          }
        })
        .catch((err) =>{
          /* untuk error 4xx */
          if (err.response){
            this.pesanError= "Username atau Password Salah";
            this.alert= true;
          }
          /* misal req error dll */
          else{
            this.pesanError= "Sistem Sedang Gangguan";
            this.alert= true;
          }
        })
    }
  }
}

</script>

<style>
.my-input.v-input .v-input__slot {
  border-radius: 100px;
  width: auto;
}

.tengah{
  text-align: center;
}
</style>
