<template>
  
  <v-col cols="4" xs="2" sm="4" md="12">
  <md-app :class="appWrapper" class="app-wrapper" >
    <div class="container-fluid" v-if="rr != null">
      <div class="w3-row">

        <div class="w3-col" style="width:20%">
          <!-- Tombol sisi kiri -->
          <div class="sidebar1">
            <div id="">
              <v-btn
                icon
                x-large
                color="white"
                href="../Chat"
                id="chat"
              >
                <v-icon dark>mdi-message-processing</v-icon>
              </v-btn>
            </div>

            <div id="">
              <v-btn
                icon
                x-large
                color="white"
                disabled
                id="kontak"
              >
                <v-icon dark>mdi-contacts</v-icon>
              </v-btn>
            </div>

            <div id="logout">
              <v-btn
                icon
                x-large
                color="white"
                @click="logout"
                id="keluar"
              >
                <v-icon dark>mdi-login-variant</v-icon>
              </v-btn>
            </div>
          </div>
        </div>

        <div class="w3-col" style="width:60%">
          <!-- Bagian List agent -->
          <div class="listuser1">
            <div style="margin-top: 80px " >
              <template>
                <div style="margin-left: 10px ">
                  <div class="tabelagent">
                    <v-simple-table height="100%" id="warnavutura">
                      <template v-slot:default>
                        <thead>
                          <tr>
                            <th class="text-left">Nama</th>
                            <th class="text-left">Email</th>
                            <!-- <th class="text-left">Password</th> -->
                            <th class="text-left"></th>
                          </tr>
                        </thead>
                        <tbody>
                          
                          <!-- LOOPING ALL AGENT -->
                          <template v-for="(item, index) in agent">
                              <tr v-if="agent" :key="index" @click="getAkun(item.email)">
                                <td> {{item.name}}</td>
                                <td> {{item.email}} </td>
                                <!-- <td> {{item.password}} </td> -->
                                <td>
                                  <v-btn icon @click="getAgent(item._id)"> <v-icon>mdi-pencil</v-icon> </v-btn>
                                  

                                  <!-- Popup -->
                                  <div class="">
                                      <v-dialog
                                        v-model="dialog"
                                        width="500"
                                       >
                                        <template v-slot:activator="{ on }">
                                          <v-btn icon v-on="on"> <v-icon>mdi-delete</v-icon> </v-btn>
                                        </template>
                                        <v-card>
                                          <v-card-title
                                            class="headline grey lighten-2"
                                            primary-title
                                          >
                                            warning
                                          </v-card-title>

                                          <v-card-text>
                                            <h2>Apakah data akan dihapus ?</h2>
                                          </v-card-text>

                                          <v-divider></v-divider>

                                          <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn
                                              color="primary"
                                              text
                                              @click="hapusAgent(item._id)"
                                            >
                                              Hapus
                                            </v-btn>

                                            <v-btn
                                              color="primary"
                                              text
                                              @click="dialog=false"
                                            >
                                              Cancel
                                            </v-btn>

                                          </v-card-actions>
                                        </v-card>
                                      </v-dialog>
                                  </div>
                                    
                                </td>
                              </tr>
                          </template>
                        </tbody>
                      </template>
                    </v-simple-table>
                  </div>
                </div>
              </template>
            </div>
          </div>
        </div>

        <div class="w3-col" style="width:20%">
          <!-- Bagian Edit Agent -->
          <div id="boxeditagent1">
            <div class="boxisian">
              <v-card class="mx-auto" width="1000" height="800">
                <v-card-text>
                  <div style="padding-top: 30px"></div>
                  <!-- Header -->
                  <div style="text-align: center ">
                    <p class="display-1 text--primary"> MANAGE AGENT {{title}} </p>
                  </div>
                  
                  <!-- Isi -->
                  <div style="padding-top: 90px"></div>
                    <div style="<!-- margin-left: 100px --> ">
                      <div class="isian">
                        <div style="text-align: center ">
                          <h3> NAMA</h3>
                            <v-text-field
                              v-model="name"
                              width="200px"
                              placeholder="Tuliskan Nama"
                            >
                            </v-text-field>
                        </div>
                        <div style="padding-top: 30px"></div>
                        <div style="text-align: center ">
                          <h3> E-MAIL</h3>
                          <v-text-field
                            v-model="email"
                            width="200px"
                            placeholder="Tuliskan Email"
                          >
                          </v-text-field>
                        </div>
                        <div style="padding-top: 30px"></div>
                        <div style="text-align: center ">
                          <h3> PASSWORD</h3>
                          <v-text-field
                            type="password"
                            v-model="password"
                            width="200px"
                            placeholder="Tuliskan Passsword"
                          >
                          </v-text-field>
                        </div>
                      </div>
                    </div>
                </v-card-text>

                <!-- Tombol -->
                <div style="padding-top: 90px"></div>
                <div style="text-align: center "> {{warning}} </div>
                <div style="text-align: center ">
                  <v-btn 
                    dark 
                    color="#FF6C00"
                    flat
                    width="200px"
                    height="50px"
                    @click="tambahAgent"
                  >
                  TAMBAH AGENT
                  </v-btn>
                </div>
                <div style="padding-top: 20px"></div>
                <div style="text-align: center ">
                  <v-btn 
                    dark 
                    color="#FF6C00"
                    flat
                    width="200px"
                    height="50px"
                    @click="ubahAgent"
                  >
                  SIMPAN PERUBAHAN
                  </v-btn>
                </div>
            
              </v-card>
            </div>
          </div>
        </div>

      </div>
    </div>
  </md-app>
  </v-col>
</template>


<script>
import moment from 'moment'
import axios from 'axios'
export default {

    data: () => ({
    dialog: false,
    rr : sessionStorage.getItem('login'),
    name : null,
    email : null,
    password: null,
    message: null,
    warning: null,
    rid: null,
    title: null,
    messageGambar: null,
    item: 1,
    agent : [],
    /* agent : [
      {nama: 'Adlin', email: 'torq@gmail.com', password: 'touring'},
      {nama: 'Pewd', email: 'tor@ymail.com', password: 'GRANDTOURER'},
      {nama: 'Von', email: 'zyl@gmail.com', password: 'layz'},
    ], */
    chat : [],
    adadi : null,
    date : '',
  }),

  methods: {

    /* CREATE AGENT */
    tambahAgent: function() {
      axios.post('http://localhost:3000/admin/agents/create',{
        name: this.name,
        email: this.email,
        password: this.password,
      })
      .then((Response) =>{
          if(Response.status == 201){
            this.name = null;
            this.email = null;
            this.password = null;
            this.warning = "Tambah Agent Sukses";
            window.location.href = "http://localhost:8080/agent"
          }
          else{
            this.warning = "Gagal";
          }
        })
    },

    /* EDIT AGENT */
    ubahAgent: function() {
      axios.post('http://localhost:3000/admin/agents/update/'+this.rid,{
        name: this.name,
        email: this.email,
        password: this.password,
      })
      .then((Response) =>{
          if(Response.status == 200){
            this.name = null;
            this.email = null;
            this.password = null;
            this.warning = "Updated";
            window.location.href = "http://localhost:8080/agent"
          }
          else{
            this.warning = "Gagal";
          }
        })
    }, 

    /* Get Agent */
    getAgent: function(id){
      axios.get('http://localhost:3000/admin/agents/'+id)
      .then((r) =>{
        this.name = r.data.data.name
        this.title = r.data.data.name
        this.email = r.data.data.email
        this.password = r.data.data.password
        this.rid = r.data.data._id
      });
    },

    /* Hapus Agent */
    hapusAgent: function(id){
      axios.delete('http://localhost:3000/admin/agents/delete/'+id)
      .then((Response) => {
        if(Response.status == 200){
          window.location.href = "http://localhost:8080/agent"
          this.dialog = false
        }
        else{
          this.warning = "Hapus gagal";
          this.dialog = false
        }
      })
    },

    

    /* -------------- Ini untuk Logout  ----------- */
    logout: function(){
      sessionStorage.clear();
      window.location.href = "http://localhost:8080"
    },

  },
  filters:{
    changeDateFilter:
    function (value){
      return moment(value).format('LT');
    },
    changeDateFilterDate:
    function (value){
      return moment(value).format('L');
    }
  },
 
  /* MELOAD SELURUH AGENT */
  mounted(){
    axios.get('http://localhost:3000/admin/agents') /* ENDPOINT BELUM ADA */
    .then((r) => {
      this.agent = r.data.data;
    });
  }


}
</script>

<style>
textarea{
  overflow: auto;
  resize: none;
}
  .container
  .app-wrapper {
    min-height: 100vh;
    position: absolute;
    
  }
  .boxisian{
    margin: 1px;
  }
  .sidebar1{
    height: 100vh;
    background-color: #473390;
    width: 70px;
    box-shadow: 0 0 10px 0;
    position: absolute;
    z-index: 1;
  }
  .listuser1{
    background-color: #F1EFFF;
    position: absolute;
    left: 70px;
    z-index: 0;
    width: 800px;
    height: 100%;
  }
  #warnavutura{
    background-color: #F1EFFF;
  }
  #boxchat1{
    background-color: #FFFFFF;
    position: absolute;
    left: 800px;
    z-index: 0;
    width: 67%;
    height: 100%;
  }
  #boxeditagent1{
    background-color: white;
    position: fixed;
    top: 7%;
    left: 1000px;
    width: 30%;
    height: 100%;
  }
  #boxuser1{
    position: absolute;
    background-color: #F1EFFF;
    top: 70px;
    width: 150%;
    height: 80%;
    box-shadow: 0 -5px 5px -5px rgb(214, 214, 214);
    overflow-x: hidden;
    overflow-y: auto;
  }
  
  .perulanganuser1{
    width: 100%;
    height: 60px;
