<template>

  <v-col cols="4" sm="4" md="12">
  <md-app  class="app-wrapper" >
    <div class="container-fluid" v-if="rr != null">
      
      <!-- Nav Bar -->
      <div class="sidebar">
          <div v-if="rb == 'admin'">
            <div id="">
              <v-btn
                icon
                x-large
                color="white"
                disabled
                id="chat"
              >
                <v-icon dark>mdi-message-processing</v-icon>
              </v-btn>
            </div>

            <div id="">
              <v-btn
                icon
                x-large
                color="white"
                @click="pindah"
                href="../Agent"
                id="kontak"
              >
                <v-icon dark>mdi-contacts</v-icon>
              </v-btn>
            </div>
          </div>

          <div id="logout">
            <v-btn
              icon
              x-large
              color="white"
              @click="logout"
              id="keluar"
            >
              <v-icon dark>mdi-login-variant</v-icon>
            </v-btn>
          
          </div>
      </div>

      <!-- List user -->
      <div id="listuser" style="border-right-style: solid;  border-color: #C1C1C1">
        <div id="logo">
          <img src="../assets/logo-vutura.png" id="logo-vutura">
        </div>

        <!-- Perulangan user -->
        <div id="boxuser">
          <div v-for="(item, index) in user" :key="index">
            <div class="perulanganuser" @click="getMessage(item._id, item.username)">
              <v-row>
                <v-col cols="12" sm="4" class="text-center">
                  <v-avatar color="#473390">
                    <span class="white--text headline text-capitalize"> {{item.username.charAt(0)}} </span>
                  </v-avatar>
                </v-col>
                 <v-col cols="12" sm="8">
                  <p class="text-h6" style="margin-top:7px;"> {{item.username}} </p>
                </v-col>
              </v-row>
            </div>
          </div>
        </div> 
      </div>

      <!-- Box Chat -->
      <div v-if="cid" id="boxchat">
          <div id="tglchat" v-if="date">{{date | changeDateFilterDate}}</div>

          <div id="header-chat">
            <h1 style="margin: 15px 0 0 30px">{{nama}}  </h1>
          </div>

          <div id="isichat" v-if="chat != []">
            <div id="console-admin"></div>
              <!-- Chat yg dikirim ditampilkan -->
              <template v-for="(item, index) in chat">
                <!-- Dari damin -->
                <div class="kirim" v-if="item.from == 'admin'" :key="index">
                  <div class="row">
                    <div class="col-2">
                      <small>{{item.date | changeDateFilter}}</small>
                    </div>
                    <v-icon v-if=" item.read == true" color="blue">mdi-check-all</v-icon>
                    <v-icon v-else >mdi-check-all</v-icon>
                      <div class="col">
                        <div class="kirim-warna">
                          <img v-if="item.img " class="preview" :src="item.img" width="300" height="200">
                          <!-- <img v-if="item.img !== null" class="preview" :src="item.img" width="300" height="200"> -->     <!-- Bug Gk mau load gambar -->
                          <h3> {{item.msg}}</h3>
                        </div>
                      </div>
                      <div class="col-3">
                        <img src="../assets/logo-vutura.png" class="image">
                      </div>
                    </div>
                  </div>
                  
                <!-- Dari Customer -->
                <div class="terima" v-else :key="index">
                  <div class="row">
                    <div class="col-1">
                      <!-- <img src="../assets/avatar1.png" class="image"> -->
                      <v-avatar color="#473390">
                        <span class="white--text headline text-capitalize"> {{ava}} </span>
                      </v-avatar>
                      <!-- <img :src="icon" class="image"> -->
                    </div>
                    <div class="col">
                        <div class="terima-warna">
                          <img v-if="item.img" class="preview" :src="item.img" width="300" height="200">
                          <h3> {{item.msg}} </h3>
                        </div>
                    </div>
                    <div class="col-2">
                      <small>{{item.date | changeDateFilter}}</small>
                    </div>
                  </div>
                </div>
              </template>

              <!-- Chat dari socket -->
              

            </div>

            
          
          <!-- BAGIAN KIRIM-KIRIM -->
            <div class="vgambar" style="position: fixed">
                  <div style="position: fixed">
                    <img v-if="messageGambar" :src="messageGambar" width="300" height="200" />
                  </div>
            </div>
            
            <div id="message">

              <div class="row" >
                    
                    <!-- Add Gambar -->
                    <div class="col-md-1">
                      <div class="">
                        <label class="custom-file-upload">
                          <input type="file" @change="viewGambar"/>
                          <i class="mdi mdi-36px mdi-image"></i>
                        </label>
                      </div>
                    </div>

                    <!-- Tulis Pesan -->
                    
                  <div class="col-md-10">
                    <v-text-field outlined height="40px" :disabled="messageGambar !== null" v-model="message" name="pesan" id="textarea1" class="materialize-textarea" cols="75"
                    placeholder="Masukkan Pesan..." @keydown="inputHandler"></v-text-field>
                  </div>
                  
                  <!-- Tombol Kirim Pesan -->
                  <div class="col">

                    <v-btn
                    v-if="message"
                    icon
                    x-large
                    color="black"
                    type="submit"
                    @click="sendMessage "
                    >
                      <v-icon >mdi-send</v-icon>
                    </v-btn>

                    <v-btn
                    v-if="messageGambar"
                    icon
                    x-large
                    color="black"
                    type="submit"
                    @click="sendPicture "
                    >
                      <v-icon >mdi-send</v-icon>
                    </v-btn>

                  </div>
                  
              </div>

            </div>
      </div>
      <div v-else id="boxchat">
        <img src="../assets/icon chat.png" id="logo-vutura2">
      </div>
    </div>
  </md-app>
  </v-col>
</template>

<script src="/socket.io/socket.io.js"></script>

<script>
import moment from 'moment'
import axios from 'axios'
import io from 'socket.io-client';
import firebase from 'firebase';


var socket = io('http://localhost:3000');   /* Koneksi */



export default {

    data: () => ({
    io,
    rr : sessionStorage.getItem('login'), /* Nyimpen id admin */
    rb : sessionStorage.getItem('user'), 
    nama : '',
    firstname : '',
    profile : '',
    message: '',
    msg: null, /* untuk didalem fungsi aja */
    img: null, /* untuk didalem fungsi aja */
    idcust: null, /* untuk didalem fungsi aja */
    icon: null,
    messageGambar: null,
    item: 1,
    user : [],
    cid: null, /* Menyimpan id cust yg dipencet */
    chat : [], /* Penampung dari API */
    chat1 : [], /* Penampung dari socket */
    adadi : null,
    date : '',
    pic : null,
    picUrl: '', /* nyimpen url hasil upload */
    picUrl2: null,
    uploadValue: 0,
    ava: '',
    status: '',
  }),

  /* Fungsi Socket */
  created(){

    /* Listen chat customer terpilih */       
    /* socket.on('customerChat', (r) => {
        this.chat1.push({
          msg: r.msg,
          from: 'customer',
          img: r.img,
        });
    }); */
  },  


  methods: {

    pindah: function(){
      socket.emit('close-admin')
    },

    viewGambar(e){
      const file = e.target.files[0];
      this.pic = e.target.files[0];
      this.messageGambar = URL.createObjectURL(file);
    },

    /* -------------- Ini untuk kirim chat ----------- */
    sendMessage: function(event) {
      const id = sessionStorage.getItem('idkirimdata');
        if(this.message){
         /*  axios.post('http://localhost:3000/admin/home/chat/'+id,{
            message: this.message
          })
          .then((Response) =>{
            console.log(Response.status)
            if(Response.status == 201){ */
              console.log(Response)
              this.chat.push({    /* langsung tampil di boxchat */
                img: null,
                from: 'admin',
                msg: this.message
              })
              
              /* Ini untuk emit text aja ke socket */
              var data = {};
              data.msg = this.message;
              /* data.idcust = this.rr; */
              data.idcust = this.cid;
              data.img = null;
              socket.emit('adminChat', data, (callback)=>{
                console.log(callback)
              });
              console.log('habis emit')

              console.log(this.chat)
              this.message = '';
            /* }
          }) */
        }
    },

    sendPicture: function(event) {
      const id = sessionStorage.getItem('idkirimdata');
      
      /* Bagian Upload */
      this.picUrl=null;
      const storageRef= firebase.storage().ref(`${this.pic.name}`).put(this.pic);
      storageRef.snapshot.ref.getDownloadURL().then((url)=>{
          /* Bagian Push ke chatbox */
          this.chat.push({
            msg: null,
            from: 'admin',
            img: url
          })
          
          /* EMIT URL HARUS DARI SINI, kalau tidak bakal null */
          /* Ini untuk emit chat ke socket */
          var data = {};
              data.msg = null;
              /* data.idcust = this.rr; */
              data.idcust = this.cid;
              data.img = url;
              socket.emit('adminChat', data, (callback)=>{
                console.log(callback)
                if (callback == true){
                  this.status = 'read'
                }
              });
              socket.on('open-cust', (data)=>{
                  console.log(data.msg)
              })
        });

      this.messageGambar = null
    },

    inputHandler: function(event){
      if (event.keyCode === 13){
        event.preventDefault()
        if(this.message){
          this.sendMessage()
        }
      }
    },


    /* -------------- Ini untuk Logout  ----------- */
    logout: function(){
      sessionStorage.clear();
      window.location.href = "http://localhost:8080"
    },

    /* Get msg id & username (klik user) */
    getMessage: function(s,u){
      sessionStorage.removeItem('listChat')
      /* close socket */
      socket.emit('close-admin'); 
      this.chat = [];
      this.date = '';
      this.nama = '';
      this.nama = u;
      this.ava = u.charAt(0);
      this.cid = s; /* Menyimpan ID yg dipencet */
      sessionStorage.removeItem('idkirimdata')
      sessionStorage.setItem('idkirimdata',s);
      sessionStorage.removeItem('listChat')
      axios.get('http://localhost:3000/admin/home/'+s)
      .then((r) => {
        console.log(r.data.data)
        this.date = r.data.data[0].date;
        this.chat = r.data.data;
        sessionStorage.setItem('listChat',JSON.stringify(r.data.data))
      });

      /* open socket */
      socket.emit('open-admin',s); 
      /* Get dari socket */
      /* socket.on('customerChat', (r) => {
        if (cid == r.s){ 
          this.chat.push({
            msg: r.msg,
            from: 'customer',
            img: r.img,
          });
        }
      }); */
    },

    setupWebSocket(){
      try{
        const socket = this.io('http://localhost:3000')
        socket.on('open-cust', (r) =>{
            console.log(r.msg)
        });

        socket.on('customerChat', (r) => {
        this.chat.push({
              msg: r.msg,
              from: 'customer',
              img: r.img,
            });
        });
        socket.on('adminChat', (b) => {
        this.chat1.push({
              msg: b.msg,
              from: 'admin',
              img: b.img,
            });
        });
        
      } 
      catch(error){
        console.log(error)
      }
    }

  },

  watch: {
    firstname: function(newValue) {
      this.r = this.getFirstLetter(newValue);
    }
  },

  filters:{
    changeDateFilter:
    function (value){
      return moment(value).format('LT');
    },
    changeDateFilterDate:
    function (value){
      return moment(value).format('L');
    }
  },

  /* MELOAD ALL CUSTOMER */
  mounted(){  
    
    axios.get('http://localhost:3000/admin/home')
    .then((r) => {
      this.user = r.data.data;
    });
    /* register as admin */
    console.log('before')
    socket.emit('register-as-admin');
    console.log('after')
    /* Open admin */
    socket.on('open-admin');
    this.setupWebSocket()
    socket.on('customerChat', (r) => {
        this.chat.push({
          msg: r.msg,
          from: 'customer',
          img: r.img,
        })
    })
    socket.on('adminChat', (b) => {
        this.chat1.push({
          msg: b.msg,
          from: 'admin',
          img: b.img,
        });
    });

  },

}
</script>

<style>
textarea{
  overflow: auto;
  resize: none;
}
  .container
  .app-wrapper {
    min-height: 100vh;
    position: absolute;
    
  }
  .sidebar{
    height: 100vh;
    background-color: #473390;
    width: 70px;
    box-shadow: 0 0 10px 0;
    position: fixed;
    z-index: 1;
  }
  #listuser{
    background-color: #F1EFFF;
    position: fixed;
    left: 70px;
    z-index: 0;
    width: 438px;
    height: 100%;
  }
  #boxchat{
    background-color: #FFFFFF;
    position: fixed;
    left: 507px;
    z-index: 0;
    width: 67%;
    height: 100%;
  }
  #chat{
    top: 80px;
    position: absolute;
    left: 12px;
  }
  #kontak{
    top: 150px;
    position: absolute;
    left: 12px;
  }
  #boxuser{
    position: absolute;
    background-color: #F1EFFF;
    top: 70px;
    width: 100%;
    height: 80%;
    box-shadow: 0 -5px 5px -5px rgb(214, 214, 214);
    overflow-x: hidden;
    overflow-y: auto;
  }
  
  .perulanganuser{
    width: 100%;
    height: 60px;
    position: static;
    background-color: #F1EFFF;
    margin-top: 2px;
    box-shadow: 0 5px 10px -10px #333;
  }
  .perulanganuser:hover,
  .perulanganuser:focus {
      text-decoration: none;
      cursor: pointer;
      background-color: #DEDAFF;
  }

  #header-chat{
    background-color: #F1EFFF;
    height: 70px;
    width: 100%;
    position: fixed;
    top: 0px;
  }
  #isichat{
    position: absolute;
    top:110px;
    width: 100%;
    padding: 10px 0px 30px 10px;
    background-color: white;
    height: 90%;
    overflow-x: hidden;
    overflow-y: auto;
  }
  .terima{
    padding: 5px 0 0 10px;
    float: left;
    width: 80%;
  }
   .kirim{
    padding: 5px 0 0 10px;
    float: right;
    position: relative;
    width: 80%;
  }
  #console {
    width: 100%;
  }
  #console-admin {
    width: 100%;
  }
  .terima-warna{
    word-break: break-word;
    border-radius: 0px 15px 15px 15px;
    padding: 10px 20px;
    background-color: #F1EFFF;
    position: static;
    text-align: left;
    width: 100%;
    max-width: 700px;
  }
  .kirim-warna{
    word-break: break-word;
    border-radius: 15px 0px 15px 15px;
    padding: 10px 20px;
    background-color: #C0B7FF;
    position: static;
    text-align: right;
    width: 100%;
    max-width: 700px;
  }
  #message{
    position: fixed;
    bottom: 0px;
    height: 8%;
    max-height: 40%;
    width: 67%;
    background-color: #F1EFFF;
  }
  #keluar{
    bottom: 12px;
    position: absolute;
    left: 12px;
  }
  #logo-vutura{
    height: 70px;
    position: absolute;
    padding: 10px;
    right: 45%;
  }
  #logo-vutura2{
    height: 300px;
    position: absolute;
    padding: 10px;
    top: 30%;
    right: 45%;
  }
  .image{
    width: 40px;
    border-radius: 30px;
  }
  #tglchat{
    position: absolute;
    top: 80px;
    background-color: #F1EFFF;
    margin: 0 -50% 0 40%;
    padding: 5px;
    border-radius: 5px;
  }
  .tombol2{
    position: fixed;
    max-width: 50px;
    max-height: 50px;
  }
  input[type="file"] {
    display: none;
  }
  .custom-file-upload {
    display: inline-block;
    cursor: pointer;
    width: 80px;
    margin-left: 50px;
  }
  .vgambar{
    position: fixed;
    padding-bottom: 10px;
    padding-left: 10px;
    bottom: 210px;
    height: 10%;
    max-height: 40%;
    width: 67%;
  }
</style>
